.homepage {
  display:flex;
  width:800px;
  flex-direction:column;
  justify-content: space-around;
  align-items: center;
  padding:5px;
  background:lightgrey;
}

.homepage>* {
  margin-bottom:10px;
}

.auth-img {
  width:400px;
  height:400px;
}

.author-of-the-day {
  text-align: center;
}

.autor-of-the-day {
  width: 80%;
  height: 100%;
  padding: 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: rgb(207, 204, 204);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.autor-of-the-day a {
  display: flex;  
  flex-direction: column;
  align-items: center;
  text-decoration: none;
}


.autor-of-the-day img {
  width: 400px;
  height: 500px;
  margin: 10px 0;
} 

.autor-of-the-day span {
  font-size: 35px;
  font-weight: bold;
  color: black;
}

.autor-of-the-day span:hover {
  color: rgb(82, 45, 45);
}
.developer {
  margin: 10px 20px;
  padding: 10px;
  background-color: rgb(143, 140, 140);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.developer a {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 19px;
  color: rgb(0, 0, 0);
  text-decoration: none;
}

.developer img {
  margin-bottom: 10px;
}

.developer a:hover {
  color: rgb(243, 243, 243);
}
.about-us {
  width: 80%;
  padding: 10px;  
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(209, 207, 207);
}

.our-developers {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.container {
  display: flex;
  width: 1100px;
  height: 80vh;
}

.col,
.row {
  display: flex;
  width: 100%;
}

.col {
  flex-direction: column;
}

.row {
  flex-direction: row;
}

.search-shape {
  margin: 200px auto 50px;
}

.search-shape input {
  padding: 7px 10px;
  width: 300px;
  border: 1px solid #cecece;
  border-radius: 5px;
  outline: none;
}

.search-result {
  width: 100%;
  display: flex;
}

.search-result .card {
  margin: 5px auto;
  width: 700px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.15);
}

.search-result .card img {
  display: flex;
  width: 100px;
  height: 100px;
}

.search-result .card .author-name {
  margin: auto 50px auto;
}
