.search-results{
  margin-top: 2%;
  width: 100%;
}

.search-results__link {
  display: block;
  font-size: 1.2em;
}

.timeline-block {
  width: 100%;
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.timeline-block h2 {
  color: rgb(241, 241, 241);
}

.timeline {
  margin-top: 25px;
}
.autor-cutaway {
  width: 100%;
  display: flex;
  margin-bottom: 60px;
}

.autor-cutaway span {
  width: 63%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 45px;
  font-weight: bold;
  color: rgb(250, 248, 248);
}

.author-avatar {
  height: 440px;
}

.author-works {
  width: 100%;
  margin-bottom: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;  
}

.author-works h2{
  color: rgb(241, 241, 241);
}

.works-list {
  width: 100%;
  padding-top: 10px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.author-works li {
  background-color: rgb(226, 226, 226);
  margin: 10px;
  padding: 10px 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.author-works i {
  color: rgb(134, 60, 60);
}

.author-works span {
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: bold;
}
.author-image {
  grid-area: image;
}

.list-of-works{
  grid-area:  works;
}

.timeline {
  grid-area: timeline;
}

.photo-list {
  grid-area: photos;
}

.video-list {
  grid-area: video;
  position:relative;
}

.map-component {
  grid-area: map;
  min-height: 40vh;
  width: 100%;
}

.author-page {
  width: 90%;
  padding: 30px;
  background-color: rgb(139, 138, 138);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.video-overlay {
  position:fixed;
  top:0;
  left:0;
  z-index:1;
  height:100%;
  width:100%;
  background:#ffffff;
  opacity:0.6;
  display:none;    
}
.video-video {
  display:none;
  position: absolute;
  top:0px;
  left:-250px;
  z-index:2;
}

.gallery-container {
  display:flex;
  height:200px;
  width:600px;
  align-items: center;
  justify-content: center;
}

.gallery-item {
  height:200px;
}

button.active {
  background-color: aquamarine;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
* {
  box-sizing: border-box;
}

